.image-container {
  height: 1000px;
  z-index: -1;
  background-image: url("../static/ohio-stadium.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.background-color {
  background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
  width: 100vw;
  height: 100vh;
}

.student-text {
  color: white;
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  padding-top: 100px;
  padding-bottom: 30px;
  font-size: 20px;
}

.seat-info {
  color: white;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding-left: 30px;
  padding-right: 30px;
}

.seat-number {
  margin-top: -15px;
  padding-bottom: 50px;
  font-size: 20px;
  font-weight: bolder;
}

.barcode-container {
  background-color: white;
  border-radius: 5px;
  text-align: center;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  max-width: 90%;
  height: 150px;
}

.screenshot-text {
  font-weight: bolder;
}

.apple-container {
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 50px;
}

.gate-container {
  color: white;
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
  font-weight: bolder;
  font-size: larger;
}
