@keyframes parent-animation {
  0% {
    left: 30px;
    top: 300px;
  }
  50% {
    left: 360px;
    top: 300px;
  }
  100% {
    left: 30px;
    top: 300px;
  }
}

@keyframes child-animation {
  0% {
    left: 20px;
    top: 300px;
  }
  50% {
    left: 355px;
    top: 300px;
  }
  100% {
    left: 20px;
    top: 300px;
  }
}

.parent {
  position: absolute;
  width: 5px;
  height: 120px;
  background-color: rgb(39, 108, 215);
  margin-top: -10px;
  animation-name: parent-animation;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease;
}

/* cubic-bezier(0.20, 0.1, 0.20, 1.0) */
.child {
  position: absolute;
  width: 15px;
  height: 100px;
  background-color: rgb(139, 180, 234);
  opacity: 100%;
  animation-name: child-animation;
  animation-duration: 3s;
  animation-delay: 0.1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease;
}

.barcode {
  z-index: 1;
  height: 100px;
  margin-top: 20px;
}
